import "./index.css";
import {
  createGameLoop,
  createBatch,
  loadTexture,
  loadAtlas,
  createViewport,
  fillRect,
} from "gdxjs";
import createWhiteText from "gl-white-texture";
import loadFontFile from "./utils/loadFontFile";
import createInstructionScreenSystem from "./screens/InstructionScreen";
import axios from "../../utils/axios";
import React from "react";
import ReactDOM from "react-dom";
import RegisterModal from "../RegisterModal";
import sakuraRenderer from "./systems/SakuraRenderSystem";

const init = async (canvas) => {
  // const canvas = document.getElementById("main");
  const width = 55;
  const height = 100;
  const viewport = createViewport(canvas, width, height);
  const camera = viewport.getCamera();
  const gl = viewport.getContext();
  const batch = createBatch(gl);
  const whiteTex = createWhiteText(gl);

  const Game = {
    score: 0,
    width,
    height,
    canvas,
    gl,
    batch,
    camera,
    whiteTex,
    viewport,
    setCurrentScreen(screen) {
      if (Game.currentScreen) {
        Game.currentScreen.destroy();
      }
      Game.currentScreen = screen;
    },
  };

  // const foxLogo = await loadTexture(gl, "./assets/logo.png");
  // const LOGO_WIDTH = width / 1.8;
  // const LOGO_HEIGHT = (LOGO_WIDTH * foxLogo.height) / foxLogo.width;

  let backGround;
  let sakuraTex;
  let sakuraRender;

  const draw = () => {
    batch.setProjection(camera.combined);
    batch.begin();
    batch.setColor(255 / 255, 114 / 255, 22 / 255, 1);
    batch.setColor(1, 1, 1, 1);
    if (backGround !== undefined) {
      fillRect(batch, backGround, 0, 0, width, height);
    } else {
      fillRect(batch, whiteTex, 0, 0, width, height);
    }

    batch.setColor(0.4, 0.933, 0.165, 1);
    fillRect(
      batch,
      whiteTex,
      width / 2 - width / (1.45 * 2),
      (height * 2.5) / 4 - height / 50,
      width / 1.45,
      height / 25
    );
    batch.setColor(1, 1, 1, 1);
    fillRect(
      batch,
      whiteTex,
      width / 2 - width / (1.46 * 2),
      (height * 2.5) / 4 - height / 52,
      width / 1.46,
      height / 26
    );
    batch.setColor(0.4, 0.933, 0.165, 1);
    let ratio = StateTime / timer;
    fillRect(
      batch,
      whiteTex,
      width / 2 - width / (1.5 * 2),
      (height * 2.5) / 4 - height / 72,
      (width / 1.5) * ratio,
      height / 36
    );
    batch.end();
  };

  let finishedLoading = false;
  let StateTime = 0;
  let timer = 1;
  gl.clearColor(0, 0, 0, 0);
  const update = (delta) => {
    StateTime = Math.min((StateTime += delta), timer);
    gl.clear(gl.COLOR_BUFFER_BIT);
    if (finishedLoading && StateTime >= timer) {
      if (Game.currentScreen) {
        Game.currentScreen.update(delta);
      }
    } else if (!finishedLoading || StateTime < timer) {
      draw();
    }
    if (sakuraTex !== undefined) {
      sakuraRender.update(delta);
    }
  };

  createGameLoop(update);

  const doAndCount = (promise) =>
    promise.then((result) => {
      return result;
    });

  backGround = await loadTexture(gl, "./assets/loadingScreen.jpg");
  sakuraTex = await loadTexture(gl, "./assets/cherryBlossom.png");
  sakuraRender = sakuraRenderer(sakuraTex, batch, camera, width, height);

  const [gameAssets, smoke] = await Promise.all([
    doAndCount(loadAtlas(gl, "./assets/eyemiru.atlas")),
    doAndCount(loadAtlas(gl, "./assets/smoke.atlas")),
  ]);

  let assets = {
    gameAssets,
    smoke,
    sakuraTex,
  };
  Game.assets = assets;

  const font = await loadFontFile(gl, "./fonts/text.fnt");
  const nameFont = await loadFontFile(gl, "./fonts/myriadPro.fnt");

  Game.fonts = { font, nameFont };

  finishedLoading = true;

  const goToInstructionScreen = async () => {
    Game.setCurrentScreen(await createInstructionScreenSystem(Game));
  };

  // const token = localStorage.getItem("@Token");
  // if (!token) {
  //   const wrapper = document.createElement("div");
  //   document.body.append(wrapper);
  //   ReactDOM.render(
  //     <RegisterModal
  //       onSubmit={async (formBody) => {
  //         try {
  //           const res = await axios.post("/register", formBody);
  //           const token = res.data;
  //           localStorage.setItem("@Token", token);
  //           document.body.removeChild(wrapper);
  //           goToInstructionScreen();
  //         } catch (e) {}
  //       }}
  //     />,
  //     wrapper
  //   );
  // } else {
  //   goToInstructionScreen();
  // }

  goToInstructionScreen();
};

export default init;
