const sakuraRenderer = (texture, batch, camera, width, height) => {
  const SAKURA_WIDTH = width / 20;
  const SAKURA_HEIGHT = (SAKURA_WIDTH * texture.height) / texture.width;
  let sakuras = [];
  let stateTime = 0;
  let spawnTime = 1;

  const draw = (delta) => {
    batch.setProjection(camera.combined);
    batch.begin();
    for (let i = sakuras.length - 1; i >= 0; i--) {
      sakuras[i].stateTime += delta;
      sakuras[i].opacity = Math.max(
        0,
        (sakuras[i].opacity -= delta * sakuras[i].opacitySpeed)
      );

      batch.setColor(1, 1, 1, sakuras[i].opacity);
      sakuras[i].x += delta * sakuras[i].speedX;
      sakuras[i].y += delta * sakuras[i].speedY;

      if (sakuras[i].stateTime >= sakuras[i].time) {
        sakuras[i].stateTime -= sakuras[i].time;
        sakuras[i].speedX *= -1;
      }

      sakuras[i].rotation += sakuras[i].rotateSpeed * delta;

      batch.draw(
        texture,
        sakuras[i].x,
        sakuras[i].y,
        SAKURA_WIDTH,
        SAKURA_HEIGHT,
        SAKURA_WIDTH / 2,
        SAKURA_HEIGHT / 2,
        sakuras[i].rotation,
        sakuras[i].scale,
        sakuras[i].scale
      );

      if (sakuras[i].opacity <= 0) {
        sakuras.splice(i, 1);
      }
    }

    batch.setColor(1, 1, 1, 1);
    batch.end();
  };

  return {
    update(delta) {
      stateTime += delta;
      if (stateTime > spawnTime) {
        const counter = Math.floor(Math.random() * (4 - 1) + 1);
        for (let i = 0; i < counter; i++) {
          sakuras.push({
            x: Math.random() * (width * 0.9 - width * 0.1) + width * 0.1,
            y: Math.random() * (height * 0.2 - height * 0.05) + height * 0.05,
            speedX: width / 10,
            speedY: height / 10,
            opacity: 1,
            rotation: Math.random() * Math.PI,
            time: 1,
            stateTime: 0,
            rotateSpeed:
              Math.random() * (Math.PI / 1.5 - Math.PI / 2) - Math.PI / 2,
            scale: Math.random() * (1 - 0.5) + 0.5,
            opacitySpeed: Math.random() * (0.5 - 0.3) + 0.3,
          });
        }
        spawnTime = stateTime + Math.random() * (2 - 1) + 1;
      }

      draw(delta);
    },
  };
};

export default sakuraRenderer;
