import axios from "../../../utils/axios";
import { createViewportAwareInputHandler, fillRect, Vector2 } from "gdxjs";
import createStartScreenSystem from "./InstructionScreen";
import { checkPointInsideRect } from "../utils/coordUtils";
import createInstructionScreenSystem from "./InstructionScreen";

import MurmurHash from "imurmurhash";
// import analytics from "../../utils/analytics";
import analytics from "../../../utils/analytics";

import { AlignMode, TextType } from "../utils/loadFontFile";

const MAGIC = `blah@gamefox`;

const createLeaderBoardSystem = (Game) => {
  const {
    canvas,
    viewport,
    width,
    height,
    camera,
    batch,
    assets,
    whiteTex,
    fonts,
    score,
  } = Game;

  const goToInstructionScreen = async () => {
    Game.setCurrentScreen(await createInstructionScreenSystem(Game));
  };

  const inputHandler = createViewportAwareInputHandler(canvas, viewport);

  const atlas = assets.gameAssets;
  const bg = atlas.findRegion("bg_leaderboard", -1);
  const border = atlas.findRegion("border", -1);
  const board = atlas.findRegion("leaderboard", -1);
  const tree = atlas.findRegion("tree2", -1);
  const fbButton = atlas.findRegion("facebook", -1);
  const iButton = atlas.findRegion("internet", -1);
  const retryButton = atlas.findRegion("replay", -1);
  const lattern = atlas.findRegion("den_long", -1);
  const flower = atlas.findRegion("flower", -1);

  const BOARD_WIDTH = width;
  const BOARD_HEIGHT = (BOARD_WIDTH * board.height) / board.width;

  const B_HEIGHT = height;
  const B_WIDTH = (B_HEIGHT * border.width) / border.height;

  const TREE_WIDTH = width;
  const TREE_HEIGHT = (TREE_WIDTH * tree.height) / tree.width;

  const BUTTON_WIDTH = width / 5.5;
  const BUTTON_HEIGHT = (BUTTON_WIDTH * fbButton.height) / fbButton.width;

  const L_WIDTH = width * 0.98;
  const L_HEIGHT = (L_WIDTH * lattern.height) / lattern.width;

  const F_WIDTH = width / 10;
  const F_HEIGHT = (F_WIDTH * flower.height) / flower.width;

  const FB_RECT = [
    width / 2 - BUTTON_WIDTH / 2 - BUTTON_WIDTH - width / 100,
    (height * 3.15) / 4,
    BUTTON_WIDTH,
    BUTTON_HEIGHT,
  ];

  const I_RECT = [
    width / 2 - BUTTON_WIDTH / 2,
    (height * 3.15) / 4,
    BUTTON_WIDTH,
    BUTTON_HEIGHT,
  ];

  const RETRY_RECT = [
    width / 2 + BUTTON_WIDTH / 2 + width / 100,
    (height * 3.15) / 4,
    BUTTON_WIDTH,
    BUTTON_HEIGHT,
  ];

  const scoreFont = fonts.font;
  const nameFont = fonts.nameFont;

  // let players = [];
  // const getData = async () => {
  //   try {
  //     analytics.track("gameOver", { score });
  //     const res = await axios.post(
  //       "/leader-board",
  //       { score, sig: MurmurHash().hash(`${score};${MAGIC}`).result() },
  //       {
  //         headers: { "x-access-token": localStorage.getItem("@Token") },
  //       }
  //     );
  //     players = res.data;
  //   } catch (e) {}
  // };
  // getData();

  let players = [
    { username: "Chủ thớt", score: 10000 },
    { username: "Wàng Thượng", score: 9999 },
    { username: "Bạn", score: Game.score },
  ];

  let coord = new Vector2();
  inputHandler.addEventListener("touchStart", () => {
    coord.setVector(inputHandler.getTouchedWorldCoord());
    if (checkPointInsideRect(coord, FB_RECT)) {
      //  https://www.facebook.com/ThankYouEyesEyemiru
      window.open("https://www.facebook.com/ThankYouEyesEyemiru", "_blank");
    } else if (checkPointInsideRect(coord, I_RECT)) {
      //  https://eyemiru.net.vn/en/
      window.open("https://eyemiru.net.vn/en/", "_blank");
    } else if (checkPointInsideRect(coord, RETRY_RECT)) {
      goToInstructionScreen();
    }
  });

  const userScore = scoreFont.setNewText(width);
  const scoreSize = width / 7.2;

  const userNameText = [
    nameFont.setNewText(width),
    nameFont.setNewText(width),
    nameFont.setNewText(width),
  ];
  const userScoreText = [
    scoreFont.setNewText(width),
    scoreFont.setNewText(width),
    scoreFont.setNewText(width),
  ];

  let scoreWidth = [];

  const namePosition = [
    (height * 1.23) / 3,
    (height * 1.53) / 3,
    (height * 1.815) / 3,
  ];
  const scorePosition = [
    (height * 1.28) / 3,
    (height * 1.58) / 3,
    (height * 1.865) / 3,
  ];

  const flowerPosition = [
    (height * 1.28) / 3,
    (height * 1.58) / 3,
    (height * 1.865) / 3,
  ];

  const scaleFlower = [1, 0.75, 0.5];
  const opacityFlower = [1, 0.75, 0.5];

  const userNameSize = width / 30;
  const userScoreSize = width / 11.5;
  let loopLength = 0; // top 3 player ll be display

  const update = (delta) => {
    batch.setProjection(camera.combined);
    batch.begin();
    bg.draw(batch, 0, 0, width, height);
    board.draw(
      batch,
      width / 2 - BOARD_WIDTH / 2,
      height / 8,
      BOARD_WIDTH,
      BOARD_HEIGHT
    );
    tree.draw(
      batch,
      width / 2 - TREE_WIDTH / 2,
      height - TREE_HEIGHT,
      TREE_WIDTH,
      TREE_HEIGHT
    );
    border.draw(batch, 0, 0, B_WIDTH, B_HEIGHT);
    border.draw(batch, width - B_WIDTH, 0, B_WIDTH, B_HEIGHT);
    lattern.draw(batch, width / 2 - L_WIDTH / 2, height / 3, L_WIDTH, L_HEIGHT);

    fbButton.draw(
      batch,
      width / 2 - BUTTON_WIDTH / 2 - BUTTON_WIDTH - width / 100,
      (height * 3.15) / 4,
      BUTTON_WIDTH,
      BUTTON_HEIGHT
    );

    iButton.draw(
      batch,
      width / 2 - BUTTON_WIDTH / 2,
      (height * 3.15) / 4,
      BUTTON_WIDTH,
      BUTTON_HEIGHT
    );

    retryButton.draw(
      batch,
      width / 2 + BUTTON_WIDTH / 2 + width / 100,
      (height * 3.15) / 4,
      BUTTON_WIDTH,
      BUTTON_HEIGHT
    );
    batch.setColor(0, 0, 0, 1);
    userScore.draw(
      batch,
      `${Game.score}`,
      (width * 1.07) / 2,
      (height * 2.77) / 4,
      scoreSize
    );
    batch.setColor(1, 1, 1, 1);

    if (players.length > 0) {
      batch.setColor(0, 0, 0, 1);
      loopLength = players.length > 3 ? 3 : players.length;
      scoreWidth.length = 0;
      for (let i = 0; i < loopLength; i++) {
        userNameText[i].setAlignMode(AlignMode.center);
        userNameText[i].draw(
          batch,
          `${players[i].username}`,
          width / 2,
          namePosition[i],
          userNameSize
        );

        userScoreText[i].setAlignMode(AlignMode.center);
        userScoreText[i].draw(
          batch,
          `${players[i].score}`,
          width / 2,
          scorePosition[i],
          userScoreSize
        );
        scoreWidth.push(userScoreText[i].getOriginWidth());
      }
      batch.setColor(1, 1, 1, 1);
    }

    for (let i = 0; i < loopLength; i++) {
      batch.setColor(1, 1, 1, opacityFlower[i]);
      flower.draw(
        batch,
        width / 2 - scoreWidth[i] / 2 - F_WIDTH,
        flowerPosition[i],
        F_WIDTH,
        F_HEIGHT,
        F_WIDTH / 2,
        F_HEIGHT / 2,
        0,
        scaleFlower[i],
        scaleFlower[i]
      );

      flower.draw(
        batch,
        width / 2 + scoreWidth[i] / 2,
        flowerPosition[i],
        F_WIDTH,
        F_HEIGHT,
        F_WIDTH / 2,
        F_HEIGHT / 2,
        0,
        scaleFlower[i],
        scaleFlower[i]
      );
    }
    batch.setColor(1, 1, 1, 1);

    batch.end();
  };
  return {
    update,
    destroy() {
      inputHandler.cleanup();
    },
  };
};
export default createLeaderBoardSystem;
