import { AlignMode, TextType } from "../utils/loadFontFile";

const createScoreRenderSystem = () => {
  let scoreText, logo, scoreTex;
  let LG_WIDTH, LG_HEIGHT, S_WIDTH, S_HEIGHT;
  return {
    init(worldState, { fonts, width, height, assets }) {
      let atlas = assets.gameAssets;
      logo = atlas.findRegion("logo", -1);
      scoreTex = atlas.findRegion("score", -1);

      LG_WIDTH = width / 2;
      LG_HEIGHT = (LG_WIDTH * logo.height) / logo.width;

      S_WIDTH = width / 5;
      S_HEIGHT = (S_WIDTH * scoreTex.height) / scoreTex.width;

      scoreText = fonts.font.setNewText(width);
      scoreText.setAlignMode(AlignMode.left);
      scoreText.setType(TextType.dynamic);
    },
    update(delta, { score }, { batch, camera, width, height }) {
      batch.setProjection(camera.combined);
      batch.begin();
      batch.setColor(0.502, 0.867, 0.216, 1);
      scoreText.draw(batch, `${score}`, width / 3.2, height / 8.5, width / 9.3);
      batch.setColor(1, 1, 1, 1);
      logo.draw(batch, width / 12, height / 35, LG_WIDTH, LG_HEIGHT);
      scoreTex.draw(batch, width / 12, height / 7.7, S_WIDTH, S_HEIGHT);
      batch.end();
    },
  };
};

export default createScoreRenderSystem;
