import { Vector2 } from "gdxjs";

const createBasketPositionSystem = () => {
  let tmp = new Vector2();

  return {
    update(delta, { basketState }, { batch, width, height, camera }) {
      tmp
        .setVector(basketState.target)
        .subVector(basketState.position)
        .nor()
        .scale(basketState.speed * delta);
      if (basketState.target.distanceSqr(basketState.position) <= tmp.len2()) {
        basketState.position.setVector(basketState.target);
      } else {
        basketState.position.addVector(tmp);
      }

      basketState.x = basketState.position.x;
      if (basketState.x <= basketState.width / 2) {
        basketState.x = basketState.width / 2;
      }
      if (basketState.x >= width - basketState.width / 2) {
        basketState.x = width - basketState.width / 2;
      }
    },
  };
};

export default createBasketPositionSystem;
